@font-face {
  font-family: "Rubik-Medium";
  src: url("../fonts/Rubik-Medium.woff") format("woff"), url("../fonts/Rubik-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik-Light";
  src: url("../fonts/Rubik-Light.woff") format("woff"), url("../fonts/Rubik-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Rubik-Regular";
  src: url("../fonts/Rubik-Regular.woff") format("woff"), url("../fonts/Rubik-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "SourceSansPro-Regular";
  src: url("../fonts/SourceSansPro-Regular.woff") format("woff"), url("../fonts/SourceSansPro-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  width: 100%;
  height: 100%;
  font-size: 62.5%;
}

body {
  max-width: 100%;
  width: 100%;
  font-family: "Rubik-Regular";
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: #000000;
  background-color: #ffffff;
}

ul {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
}

.container {
  padding: 0 1.5rem;
  margin: 0 auto;
  max-width: 123rem;
}

.link {
  font-family: "Rubik-Medium";
  font-size: 1.5rem;
  line-height: 2.5rem;
}

.link span:nth-of-type(1) {
  padding: 1rem;
  padding-left: 1.5rem;
  color: #000000;
  background-color: #ffffff;
}

.link span:nth-of-type(2) {
  padding: 1rem;
  background-color: transparent;
}

.link svg {
  width: 15px;
  height: 11px;
}

.title {
  font-family: "Rubik-Medium";
  font-size: 3rem;
  line-height: 4rem;
}

.visually-hidden:not(:active):not(:focus),
input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  position: relative;
  z-index: 10;
  padding-bottom: 21rem;
  color: #ffffff;
  background-color: #000000;
  background-image: linear-gradient(189deg, #000000 80%, #ffffff 20%);
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 7.5rem;
  padding: 2.5rem 0;
}

.navigation__menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  max-width: 43rem;
  width: 100%;
  line-height: 2rem;
}

.navigation__link:hover {
  border-top: 2px dashed #ffffff;
}

.preview {
  position: relative;
}

.preview__wrapper {
  display: flex;
  justify-content: space-between;
}

.preview__left {
  padding-top: 25rem;
  max-width: 53rem;
  text-align: center;
}

.preview__title {
  margin: 0;
  margin-bottom: 4rem;
  font-family: "Rubik-Medium";
  font-size: 6rem;
  line-height: 6.5rem;
  color: #ffffff;
}

.preview__description {
  margin-bottom: 3.5rem;
}

.preview__button use {
  fill: #ffffff;
}

.preview__list {
  position: absolute;
  top: 23rem;
  right: 3rem;
}

.social {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 11rem;
  height: 100%;
  max-height: 22rem;
}

.social__link {
  display: flex;
  align-items: center;
}

.social__link:hover .social__text {
  display: inline;
}

.social__link:hover use {
  fill: #ffffff;
}

.social__img {
  width: 25px;
  height: 25px;
  margin-right: 1.5rem;
}

.social__img use {
  fill: #ababab;
  transition: all .15s linear;
}

.social__text {
  display: none;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #ababab;
}

.development {
  position: relative;
  z-index: 50;
  margin-bottom: 16rem;
  transform: translatey(-100%);
  color: #000000;
}

.development__list {
  display: flex;
  align-items: center;
  max-width: 117rem;
}

.development__item {
  padding: 5.5rem;
  background-color: #ffffff;
  box-shadow: 0px 0px 9px 0px rgba(73, 73, 73, 0.4);
}

.development__title {
  margin-bottom: 2rem;
  max-width: 23rem;
  font-family: "Rubik-Medium";
  font-size: 2rem;
  line-height: 2.8rem;
}

.development__description {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  line-height: 2.6rem;
  color: #585858;
}

.development__link {
  display: inline-block;
  font-family: "Rubik-Medium";
  font-size: 1.3rem;
  line-height: 2.4rem;
  text-transform: uppercase;
}

.about {
  padding: 10rem 0;
  background-image: linear-gradient(to right, #000000 25%, transparent 25%, #ffffff 80%);
}

.about__wrapper {
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
}

.about__left {
  max-width: 50rem;
  width: 100%;
}

.about__right {
  padding-top: 7rem;
  max-width: 59rem;
  width: 100%;
}

.about__title {
  margin-bottom: 2.5rem;
}

.about__text {
  margin-bottom: 3.5rem;
  max-width: 59rem;
  line-height: 2.6rem;
  color: #585858;
}

.about__button span:nth-of-type(1) {
  color: #ffffff;
  background-color: #000000;
}

.about__button use {
  fill: #000000;
}

.skills {
  position: relative;
  z-index: 10;
  max-width: 50rem;
  padding: 5rem 3rem;
  font-family: "Rubik-Medium";
  box-shadow: 0px 0px 4px 0.3px #656565;
}

.skills__title {
  margin-bottom: 1.5rem;
  font-size: 2.4rem;
  line-height: 3.4rem;
}

.skills__item:not(:last-of-type) {
  margin-bottom: 1rem;
}

.skills__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.skills__name {
  line-height: 2.6rem;
}

.skills__percent {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  font-size: 1.2rem;
  line-height: 2.4rem;
  background-color: #ebebeb;
  border-radius: 50%;
}

.skills__range {
  width: 100%;
  height: 1rem;
  background-color: #ebebeb;
  border-radius: 5px;
}

.skills__range-current {
  height: 100%;
  background-color: #ababab;
  border-radius: 5px;
}

.skills .skills__item:nth-of-type(1) .skills__range-current {
  width: 80%;
}

.skills .skills__item:nth-of-type(2) .skills__range-current {
  width: 85%;
}

.skills .skills__item:nth-of-type(3) .skills__range-current {
  width: 95%;
}

.skills .skills__item:nth-of-type(4) .skills__range-current {
  width: 80%;
}

.skills .skills__item:nth-of-type(5) .skills__range-current {
  width: 90%;
}

.services {
  padding-top: 10rem;
  padding-bottom: 15rem;
  text-align: center;
  background-color: #ffffff;
}

.services__title {
  margin-bottom: 2.5rem;
}

.services__description {
  margin: 0 auto;
  margin-bottom: 7rem;
  max-width: 40rem;
  line-height: 2.6rem;
  color: #262626;
}

.services__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.services__item {
  position: relative;
  margin-bottom: 3rem;
  padding: 3rem 4rem;
  padding-top: 8rem;
  max-width: 37rem;
  transition: all .15s linear;
  background-color: #ffffff;
  box-shadow: 0px 0px 9px 0px rgba(73, 73, 73, 0.4);
}

.services__item:hover {
  color: #ffffff;
  background-color: #000000;
}

.services__item:hover::before {
  background-image: url("../img/services-icon-white.svg");
}

.services__item::before {
  content: '';
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 35px;
  background-image: url("../img/services-icon.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.services__subtitle {
  margin-bottom: 2rem;
  font-family: "Rubik-Medium";
  font-size: 1.8rem;
}

.portfolio {
  padding: 10rem 0;
  color: #ffffff;
  text-align: center;
  background-color: #020202;
}

.portfolio__title {
  margin-bottom: 2.5rem;
}

.portfolio__description {
  margin: 0 auto;
  margin-bottom: 6rem;
  max-width: 40rem;
  line-height: 2.6rem;
}

.portfolio__sort {
  margin: 0 auto;
  margin-bottom: 4rem;
}

.portfolio .sort {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  max-width: 71rem;
}

.portfolio .sort__link {
  font-size: 1.8rem;
  line-height: 2.6rem;
}

.portfolio .sort__link--active {
  color: #ffa801;
}

.portfolio .works {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.portfolio .works__item {
  margin-bottom: 3rem;
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
}

.portfolio .works__image {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.portfolio .works__image::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rbga(#585858, 0.2);
}

.news {
  padding-top: 13.5rem;
  padding-bottom: 15rem;
  text-align: center;
  background-color: #ffffff;
}

.news__title {
  margin-bottom: 2.5rem;
  font-family: "Rubik-Medium";
  font-size: 4rem;
  line-height: 3rem;
}

.news__description {
  margin: 0 auto;
  margin-bottom: 7rem;
  max-width: 51rem;
  color: #585858;
}

.news__columns {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.news__left .news__article {
  max-width: 47rem;
}

.news__right {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
}

.news__right .article {
  display: flex;
  max-width: 67rem;
  box-shadow: 0px 0px 9px 0px rgba(195, 195, 195, 0.26);
}

.news__right .article__preview img {
  max-width: 26.3rem;
}

.news__right .article__content {
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.news__right .article__information {
  margin-bottom: 2rem;
}

.news__right .article__author::before,
.news__right .article__time::before,
.news__right .article__date::before {
  opacity: 0.6;
}

.news__right .article__title {
  margin-bottom: 1.5rem;
}

.news__right .article__description {
  margin-bottom: 1.5rem;
}

.article {
  padding: 0.8rem;
  box-shadow: 0px 0px 7px 0px #b9b9ba;
}

.article__preview img {
  max-width: 45.6rem;
  object-fit: cover;
}

.article__content {
  padding: 2rem;
  padding-top: 2.5rem;
  text-align: left;
}

.article__information {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 2.5rem;
  width: 100%;
}

.article__publication {
  line-height: 0;
}

.article__author,
.article__time,
.article__date {
  position: relative;
  padding-left: 2.5rem;
  font-family: "SourceSansPro-Regular";
  font-weight: normal;
  font-size: 1.4rem;
  line-height: 2.4rem;
  color: #585858;
}

.article__author::before,
.article__time::before,
.article__date::before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.article__author {
  margin-right: 3.5rem;
}

.article__author::before {
  top: 1px;
  background-image: url("../img/article-author.png");
}

.article__time {
  margin-right: 2.5rem;
}

.article__time::before {
  background-image: url("../img/article-time.png");
}

.article__date::before {
  background-image: url("../img/article-date.png");
}

.article__title {
  margin-bottom: 3rem;
  font-family: "Rubik-Medium";
  font-size: 2rem;
}

.article__description {
  margin-bottom: 2.5rem;
  font-family: "Rubik-Light";
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #585858;
}

.article__link {
  font-size: 1.6rem;
  text-transform: capitalize;
  color: #444444;
}